.App {
  background: rgb(24, 0, 36);
  background: -moz-linear-gradient(
    90deg,
    rgba(24, 0, 36, 1) 0%,
    rgba(109, 9, 121, 1) 100%,
    rgba(141, 15, 201, 1) 100%
  );
  background: -webkit-linear-gradient(
    90deg,
    rgba(24, 0, 36, 1) 0%,
    rgba(109, 9, 121, 1) 100%,
    rgba(141, 15, 201, 1) 100%
  );
  background: linear-gradient(
    90deg,
    rgba(24, 0, 36, 1) 0%,
    rgba(109, 9, 121, 1) 100%,
    rgba(141, 15, 201, 1) 100%
  );
  filter: progid: DXImageTransform.Microsoft.gradient(startColorstr="#180024", endColorstr="#8d0fc9", GradientType=1);
  min-height: 100vh;
  position: relative;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
